@use '../util' as *;

h1, h2, h3 {
    margin-top: 0;
    line-height: 1.1;
}

h1 {
    font-size: rem(24);
    margin-bottom: rem(3);
    color: var(--text-color2);
    @include breakpoint(large){
        font-size: rem(28);
    }
}

h2 {
    font-size: rem(24);
    margin-bottom: rem(24); // places 24 px of space on botton
}

a, a:visited, a:active {
    text-decoration: none; // no underline
}